import React, { useState, useEffect } from "react";
import { useConversations } from "../../contexts/ConversationsProvider";
import Timer from "../timer";
import "./style.css";
export default function Message({ message, uid, time }) {
  const [ended, setEnded] = useState(false);
  const { removeMessage } = useConversations();
  useEffect(() => {
    if (ended) {
      setTimeout(() => {
        removeMessage();
      }, 500);
    }
  }, [ended]);
  return (
    <div className={ended ? "message-container message-ended" : "message-container"}>
      <div className="message-name">{uid}</div>
      <Timer messageTime={time} setEnded={setEnded} />
      <div className="message-context">{message}</div>
    </div>
  );
}
