import React, { useRef, useEffect, useState } from "react";
import "./style.css";

const size = 20;
export default function Timer({ messageTime, setEnded }) {
  const [anima, setAnima] = useState();
  const [closeTheEnd, setCloseTheEnd] = useState(false);
  let α,
    π = Math.PI,
    t = 180;

  useEffect(() => {
    α = (new Date().getTime() - messageTime) / 166;
    draw();
  }, []);

  function draw() {
    α++;
    α %= 360;
    let r = (α * π) / 180,
      x = Math.sin(r) * (size / 2),
      y = Math.cos(r) * -(size / 2),
      mid = α > 180 ? 1 : 0,
      anim = `M 0 0 v -${size / 2} A ${size / 2} ${size / 2} 1 ${mid} 1 ${x} ${y} z`;
    setAnima(anim);
    setTimeout(draw, t);
    if (α > 358) {
      setEnded(true);
    }
    if (α > 270) {
      setCloseTheEnd(true);
    }
  }

  return (
    <div className={closeTheEnd ? "timer flashing-timer" : "timer"}>
      <div className="circle-background" style={{ width: size, height: size }}>
        <svg width={size} height={size} viewBox={`0 0 ${size} ${size}`}>
          <path id="border" d={anima} transform={`translate(${size / 2},${size / 2})`} />
          <path id="loader" d={anima} transform={`translate(${size / 2},${size / 2}) scale(.84)`} />
        </svg>
      </div>
    </div>
  );
}
