import React, { useEffect, useState } from "react";
import "./style.css";
import { useSocket } from "../../contexts/SocketProvider";
const roomName = "xpttg";
const HASH = ["63647852", "77f9hs9f", "ju538kf3", "kjh789dk", "25463794"];

export default function Header() {
  const [hashIndex, setHashIndex] = useState(0);
  const [loop, setLoop] = useState();
  const [users, setUsers] = useState();

  const socket = useSocket();
  useEffect(() => {
    if (socket === null) return;

    socket.on("online-users", (data) => {
      setUsers(data);
    });

    setLoop(
      setInterval(() => {
        setHashIndex((prev) => prev + 1);
      }, 5000)
    );
    return () => {
      socket.off("online-users");
      clearInterval(loop);
    };
  }, [socket]);

  return (
    <header className="header">
      <section className="header-top">
        <div className="circle-progress"></div>
        <div className="title">black space</div>
      </section>
      <div className="progress">
        <div className="indeterminate"></div>
      </div>
      <section className="information">
        <div className="square"></div>
        <div className="hash">{HASH[hashIndex % HASH.length]}</div>
        {users && <div className="users">{getUserString(users)}</div>}
        <div className="room">{roomName}</div>
      </section>
    </header>
  );
}

function getUserString(users) {
  if (users.length < 4) {
    return users.join(",");
  } else {
    return users.slice(0, 2).join(",") + `...(${users.length})`;
  }
}
